import React from "react";
import { useStaticQuery, graphql } from "gatsby";

const PiLab = () => {
  const data = useStaticQuery(graphql`
    {
      allCustomApi {
        nodes {
          data {
            key
            title
            type
            slug
            tags
            permalink
            username
            coverImage
          }
          children {
            id
          }
        }
      }
    }
  `);
  const pilab_data = data.allCustomApi.nodes[0].data;
  return pilab_data.map(item => (
    <li className="pilab-list">
      <a href={item.permalink} key={item.toString()} target="_blank">
        <img src={item.coverImage} className="pilab-img" />
      </a>
    </li>
  ));
};

export default PiLab;
